var setPreview = function(id, e) {

  var el = e.target;
  let parent = el.parentElement;
      parent = parent.parentElement;

  const thumbImg = parent.querySelector('.card-images__img');
  const thumbImgSrc = thumbImg.src;

  const MainImg = document.querySelector('.cardMainImgJs');

  var xhr = new XMLHttpRequest();
  xhr.open('POST', '/client/object/set-preview', true);
  xhr.setRequestHeader("X-Requested-With", "XMLHttpRequest");
  xhr.setRequestHeader( "Content-Type",  "application/x-www-form-urlencoded" );
  xhr.send("id="+id);
  xhr.onloadend = function() {
    if (JSON.parse(xhr.response) == true) {
      MainImg.src = thumbImgSrc;
    }
  }
}

